	module.exports = {"data":{"wordpressPost":{"title":"I had laser to remove my tattoos","content":"<p>I got my first tattoo when I was barely 19 and I thought I was pretty damn tough. Back then only bikies and ex-cons sported ink &#8211; bikies, ex-cons and me. I delighted in announcing the fact I had a tattoo to all and sundry and insisted on showing not just my friends but complete strangers.</p>\n<p>Some 20 odd years later and my tattoo collection (six) seemed less rebel chic and more totally naff and so I decided they had to go. I did a little research and decided on a course of PicoSure laser treatments. At my first session the doctor explained that my tattoos would likely need five to seven treatments before disappearing altogether. He also let me know that the treatment can be ‘uncomfortable’. Still, I wanted those suckers gone and so I told him to press on.</p>\n<p>He fired up the laser, I popped on some dark glasses in order to protect my eyes, and then we got started. As he worked he explained that PicoSure uses patented technology to deliver short pulses of energy into the skin. This energy targets the tattoo ink which then breaks up into tiny particles. These are so small they are then absorbed into the body.</p>\n<p>Of course, I didn’t really hear anything much because the laser was making a weird crackling noise and it hurt like a million burning suns. Still, five super speedy treatments later, and my clean skin fantasy is close to becoming reality and the ahem&#8230; discomfort has been worth it.</p>\n<p>PicoSure Tattoo Removal costs vary depending on the size of your tattoo and who carries out your treatment, but you’re likely looking at $500 a go. Do some research before you book in, advises Dr Suzy Bekir of The Taylor Clinic, Double Bay.</p>\n<p>Ask which type of laser will be used, what type of training and qualifications the practitioner has and how experienced they are at tattoo removal with this particular laser.</p>\n<p>Learn more at taylorclinic.com.au</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/01/19103613/d697a105493827f5480e4047cf9dd394.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"5eb20867-10cc-51a4-bf15-11c73f28fdb2"}}

